<template>
	<div
		class="FormSelect"
		@keydown.tab="tabKeyPressed = true"
		@blur.capture="handleBlur"
	>
		<div class="FormSelect__control">
			<button
				ref="button"
				:id="`${_uid}-button`"
				aria-haspopup="listbox"
				:aria-labelledby="`${_uid}-label ${_uid}-button`"
				:aria-expanded="optionsVisible"
				class="FormSelect__button"
				@click="toggleOptions"
				@keyup.up.down.prevent="showOptions"
				@keyup.up.prevent="selectPrevOption"
				@keyup.down.prevent="selectNextOption"
			>
				{{ value.label }}
				<span v-if="isEmptyObject(value)" class="FormSelect__placeholder">{{
					placeholder
				}}</span>
				<SvgAngle
					class="FormSelect__icon"
					:class="{ 'FormSelect__icon--rotate-180': optionsVisible }"
				/>
			</button>
			<!-- Focus trap for iOS keyboard navigation. -->
			<input
				v-if="!tabKeyPressed"
				aria-hidden="true"
				class="u-visually-hidden"
				@focus="handleFocusTrap"
			/>
			<ul
				v-show="optionsVisible"
				ref="options"
				tabindex="-1"
				role="listbox"
				:aria-labelledby="`${_uid}-label`"
				:aria-activedescendant="activeDescendant"
				class="FormSelect__options"
				@focus="setupFocus"
				@keyup.up.prevent="selectPrevOption"
				@keyup.down.prevent="selectNextOption"
				@keydown="search"
				@keydown.up.down.prevent
				@keydown.enter.esc.prevent="reset"
			>
				<li
					v-for="(option, index) in options"
					:key="option.label || option"
					:id="`${_uid}-option-${index}`"
					:aria-selected="activeOptionIndex === index"
					:class="activeOptionIndex === index && 'has-focus'"
					class="FormSelect__option"
					role="option"
					@click="handleOptionClick(option)"
				>
					{{ option.label || option }}
				</li>
			</ul>
		</div>
	</div>
</template>

<script>
import SvgAngle from '@/assets/icon/SvgAngle';

let resetKeysSoFarTimer;

export default {
	name: 'FormSelect',
	components: {
		SvgAngle,
	},
	model: {
		event: 'change',
	},
	props: {
		label: {
			type: String,
		},
		placeholder: {
			type: String,
			default: 'Select',
		},
		options: {
			type: Array,
			default: () => [],
		},
		value: {
			type: Object,
		},
	},
	data() {
		return {
			keysSoFar: '',
			tabKeyPressed: false,
			optionsVisible: false,
		};
	},
	computed: {
		activeOptionIndex() {
			return this.options.findIndex(
				x => x.value === this.value || x === this.value,
			);
		},
		prevOptionIndex() {
			const next = this.activeOptionIndex - 1;
			return next >= 0 ? next : this.options.length - 1;
		},
		nextOptionIndex() {
			const next = this.activeOptionIndex + 1;
			return next <= this.options.length - 1 ? next : 0;
		},
		activeDescendant() {
			return `${this._uid}-option-${this.activeOptionIndex}`;
		},
	},
	methods: {
		handleFocusTrap(e) {
			console.log(e);
			this.optionsVisible = true;
			this.$refs.button.focus();
		},
		handleOptionClick(option) {
			this.$emit('change', option);
			this.reset();
		},
		handleBlur(e) {
			if (this.$el.contains(e.relatedTarget)) return;
			this.hideOptions();
		},
		toggleOptions() {
			this.optionsVisible ? this.hideOptions() : this.showOptions();
		},
		async showOptions() {
			this.optionsVisible = true;
			await this.$nextTick();
			this.$refs.options.focus();
		},
		hideOptions() {
			this.optionsVisible = false;
		},
		async reset() {
			this.hideOptions();
			await this.$nextTick();
			this.$refs.button.focus();
		},
		setupFocus() {
			if (this.value) return;
			this.$emit('change', this.options[0]);
		},
		selectPrevOption() {
			this.$emit('change', this.options[this.prevOptionIndex]);
		},
		selectNextOption() {
			this.$emit('change', this.options[this.nextOptionIndex]);
		},
		search(e) {
			clearTimeout(resetKeysSoFarTimer);
			// No alphanumeric key was pressed.
			if (e.key.length > 1) return;

			resetKeysSoFarTimer = setTimeout(() => {
				this.keysSoFar = '';
			}, 500);

			this.keysSoFar += e.key;
			const matchingOption = this.options.find(x =>
				(x.value || x).toLowerCase().startsWith(this.keysSoFar),
			);

			if (!matchingOption) return;

			this.$emit('change', matchingOption);
		},
		isEmptyObject(param) {
			return Object.keys(param).length === 0 && param.constructor === Object;
		},
	},
};
</script>

<style lang="scss">
$placeholder-color: #979797;
$form-control-padding: 0.75em;

.u-visually-hidden {
	border: 0;
	clip: rect(0, 0, 0, 0);
	height: 1px;
	margin: -1px;
	overflow: hidden;
	padding: 0;
	position: absolute;
	white-space: nowrap;
	width: 1px;
}
.FormSelect {
	&__control {
		width: 200px;
		border: 2px solid #bebebe;
		border-radius: 0.1875em;
		outline: none;
		background-color: #fff;
		transition: border-color 0.2s;
		line-height: 1;
		padding: 0;
	}

	&__button {
		width: 100%;
		display: flex;
		padding: $form-control-padding;
		justify-content: space-between;
		background-color: transparent;
		border: none;
		outline: none;
	}

	&__placeholder {
		line-height: 12px;
		color: $placeholder-color;
	}

	&__icon {
		transition: transform 0.2s;

		&--rotate-180 {
			transform: rotate(180deg);
		}
	}

	&__options {
		margin: 0;
		padding: 0;
		list-style-type: none;
		outline: none;
		position: relative;
		background: white;
		z-index: 999;
	}

	&__option {
		padding: $form-control-padding;

		cursor: default;

		&:hover {
			background-color: rgba(#ddd, 0.5);
		}
	}
}
</style>
