<template>
	<div class="file-wrapper">
		<div class="preview-container">
			<div v-if="files[0]" class="file-preview-wrapper">
				<label :for="refName"> <img :src="files[0].preview"/></label>
				<div class="img-info">
					<span>{{ files[0].file.name }}</span>

					<div @click="fileDeleteButton">
						<Close :name="files[0].number" />
					</div>
				</div>
			</div>
			<div v-else class="file-upload">
				<div class="image-box">
					<label :for="refName">
						<ImagePlus />
					</label>
					<input
						type="file"
						:id="refName"
						ref="files"
						@change="imageAddUpload"
					/>
				</div>
			</div>
		</div>
	</div>
</template>

<script>
import ImagePlus from '@/assets/icon/ImagePlus.vue';
import Close from '@/assets/icon/Close.vue';

export default {
	components: { ImagePlus, Close },
	props: {
		refName: {
			type: String,
		},
		image: {
			type: Object,
		},
	},
	created() {
		console.log(this.refName);
		if (this.image && this.image.imgName) {
			this.image.name = this.image.imgName;
			this.files = [
				{
					file: this.image,
					preview: this.image.imgPath,
					number: 1,
				},
			];
		}
	},
	data() {
		return {
			files: [], //업로드용 파일
			filesPreview: [],
			uploadImageIndex: 0, // 이미지 업로드를 위한 변수
		};
	},
	methods: {
		imageAddUpload() {
			this.files = [
				{
					//실제 파일
					file: this.$refs.files.files[0],
					//이미지 프리뷰
					preview: URL.createObjectURL(this.$refs.files.files[0]),
					//삭제및 관리를 위한 number
					number: 1,
				},
			];
			console.log(this.refName);
			this.$emit(this.refName, this.files[0].file);
		},
		fileDeleteButton(e) {
			this.$emit(this.refName, {});
			const name = e.target.getAttribute('name');
			this.files = this.files.filter(data => data.number !== Number(name));
		},
	},
};
</script>

<style lang="scss" scoped>
.file-wrapper {
	width: 100%;
	margin-bottom: 30px;
	.preview-container {
		display: flex;
		.file-preview-wrapper {
			position: relative;
			display: inline-block;
			width: 100px;
			margin-right: 10px;
			img {
				width: 100px;
				height: 100px;
				border: 2px solid #bebebe;
				box-sizing: border-box;
			}

			.img-info {
				.file-close-button {
				}
			}
		}

		.file-upload {
			.image-box {
				label {
					display: inline-block;
					width: 100px;
					height: 100px;
					line-height: 100px;
					box-sizing: border-box;
					text-align: center;
					background: #fafafa;
					border: 2px solid #bebebe;
				}
			}
		}
	}
}
</style>
