// 메인화면

import { instance, instanceWithAuth } from './index';

function fetchColumnTotalCount() {
	return instance.get(`api/column/totalCount`);
}

function fetchColumn(page) {
	return instance.get(`api/column/all/${page}`);
}

function fetchColumnDetail(id) {
	return instance.get(`api/column/detail/${id}`);
}

function addColumn(data) {
	return instanceWithAuth.post('api/admin/column/new', data);
}

function updateColumn(id, data) {
	return instanceWithAuth.put(`api/admin/column/update/${id}`, data);
}

function deleteColumn(data) {
	return instanceWithAuth.delete('api/admin/column/delete', { data });
}

function fetchColumnWriter(page) {
	return instanceWithAuth.get(`api/columnWriter/all/${page}`);
}

function fetchColumnWriterAll() {
	return instanceWithAuth.get('api/columnWriter/all');
}

function fetchColumnWriterDetail(id) {
	return instanceWithAuth.get(`api/columnWriter/detail/${id}`);
}

function addColumnWriter(data) {
	console.log(data);
	return instanceWithAuth.post('api/admin/columnWriter/new', data);
}

function updateColumnWriter(id, data) {
	return instanceWithAuth.put(`api/admin/columnWriter/update/${id}`, data);
}

function deleteColumnWriter(data) {
	return instanceWithAuth.delete('api/admin/columnWriter/delete', { data });
}

export {
	fetchColumnTotalCount,
	fetchColumn,
	fetchColumnDetail,
	fetchColumnWriter,
	fetchColumnWriterAll,
	addColumn,
	updateColumn,
	deleteColumn,
	addColumnWriter,
	updateColumnWriter,
	deleteColumnWriter,
	fetchColumnWriterDetail,
};
