<template>
	<div class="sub-container">
		<ul class="tabs sub">
			<li @click="goto('/admin/youtube')">유튜브</li>
			<li @click="goto('/admin/column')" class="current">칼럼</li>
			<li @click="goto('/admin/writer')">칼럼 작성자</li>
		</ul>

		<div class="contents-container">
			<p class="contents-title underline marB-20 body3">칼럼 콘텐츠 등록</p>
			<div class="input-container marB-40">
				<section class="marB-40">
					<div class="input-item align-top">
						<AddImageSingle
							v-if="thumb"
							:refName="'mainImage'"
							@mainImage="setMainImage"
							:image="thumb"
						/>
					</div>
				</section>

				<section class="basic-info">
					<div class="input-item">
						<p class="small-title">제목</p>
						<input
							type="text"
							class="admin-input large"
							name="contents"
							v-model="title"
						/>
					</div>

					<div class="input-item">
						<p class="small-title">내용</p>
						<CKEditor :contents="contents" ref="editor" />
					</div>
					<div class="input-item">
						<p class="small-title">칼럼 작성자 정보</p>
						<SelectBoxAdmin
							class="small marB-20"
							:options="options"
							v-model="selected"
							placeholder="카테고리를 선택해주세요"
						/>
					</div>
				</section>

				<button class="outlined-btn" type="button" @click="updateColumn">
					등록
				</button>
			</div>
		</div>
	</div>
</template>

<script>
import { mapGetters } from 'vuex';
import Store from '@/store';
import {
	updateColumn,
	fetchColumnDetail,
	fetchColumnWriter,
} from '@/api/column';
import SelectBoxAdmin from '@/components/common/SelectBoxV2';
import AddImageSingle from '@/components/common/AddImageSingle';
import CKEditor from '@/components/common/CKEditor';

export default {
	components: { AddImageSingle, SelectBoxAdmin, CKEditor },
	computed: {
		...mapGetters(['main']),
	},
	created() {
		Store.commit('SET_MAIN', null);
		this.id = this.$route.params.id;
		this.fetchColumnDetail(this.id);
	},
	data: () => ({
		options: [],
		selected: 'NONE',
		title: '',
		contents: '',
		errMsg: '',
		id: '',
		thumb: '',
		isLoading: false,
	}),
	methods: {
		goto(url) {
			this.$router.push({ path: url });
		},
		updateSelected: function(message) {
			this.selected = message;
		},
		async fetchColumnDetail(id) {
			this.isLoading = true;
			try {
				let result = await fetchColumnWriter(0);
				this.options = result.data.map(item => {
					item.label = item.name;
					return item;
				});

				let detail = await fetchColumnDetail(id);
				let selected = this.options.filter(
					item => item.id == detail.data.columnWriter.id,
				);
				this.selected = selected[0];
				console.log(this.selected);
				this.title = detail.data.title;
				this.contents = detail.data.contents;
				this.$refs.editor.setContents(detail.data.contents);
				this.thumb = {
					imgPath: detail.data.imgPath,
					imgName: detail.data.imgName,
				};
			} catch (e) {
				console.log(e);
			} finally {
				this.isLoading = false;
			}
		},
		async updateColumn() {
			this.contents = this.$refs.editor.getContents();

			if (!this.main) {
				this.errMsg = '이미지를 등록해주세요';
				alert(this.errMsg);
				return false;
			}

			if (!this.selected) {
				this.errMsg = '카테고리를 선택해주세요';
				console.log(this.selected == 'NONE');
				alert(this.errMsg);
				return false;
			}
			if (!this.title) {
				this.errMsg = '제목을 입력해주세요';
				alert(this.errMsg);
				return false;
			}
			if (!this.contents) {
				this.errMsg = '내용을 입력해주세요';
				alert(this.errMsg);
				return false;
			}
			let data = new FormData();
			if (this.main) {
				data.append('file', this.main);
			}
			data.append('title', this.title);
			data.append('columnWriterId', this.selected.id);
			data.append('contents', this.contents);
			data.append('mainYn', 'Y');
			let result;
			this.isLoading = true;
			try {
				result = await updateColumn(this.id, data);
				if (result.status == 200) {
					alert('배너가 등록되었습니다');
				} else {
					alert('에러가 발생했습니다');
				}
			} catch (e) {
				alert('에러가 발생했습니다');
			} finally {
				this.isLoading = false;
			}
		},
		setMainImage(data) {
			Store.commit('SET_MAIN', data);
			this.thumb = this.main;
		},
	},
};
</script>

<style></style>
